* {
    box-sizing: border-box;
}
@import 'responsif';

 
.background_dasar{
    background-color:#e2dcdc;
    width:100%;
   position: relative;
    padding-top:20px;
    min-height:100vh;
    display: flex;
    flex-direction: column;
    height:auto;
    

    .background_atas{
        position: fixed;
        z-index: 2;
        //z-index: 2;
        width: 100%;
    
       top:0px;
