        display: flex;
        align-items: center;
        justify-content: space-between;
        border-radius: 0px 0px 15px 15px;
        .logo{
            text-decoration: none;
            align-items: center;
            width: 60px;
            height: 75px;
            margin-left:24px;
            background-image: url('../images/logo.png');
            background-size:contain;
            background-position:top;
            background: cover;  
            aspect-ratio: auto 136 / 48; 
        }
        .menu{
            display:flex;
            align-items: center;
            //padding:400px;
            .style{
                text-decoration: none;
                font-size: 15px;
                font-weight: bold;
                color: #D0D3D4;
                padding: 15px;
                position : relative;
                float: left;
                align-items: center;   
            }  
        }
         .menu2{
            .style2{
                
                text-decoration: none;
                font-size: 15px;
                //font-weight: bold;
                color: #D0D3D4;
                padding: 15px;
                font-weight: bold;
                //align-items: center;
                position : relative;
                float: left;   
            }
         } 
        .search{
            border:1px;
            position: absolute;
            background-color:rgb(255, 255, 255);
            margin-left : 450px;
            margin-top:30px;
            padding: 10px 40px;
            outline:none;
            font-family: 'Lato',sans-serif;
            font-size: 21px;
            color: #282828;
            border-radius: 25px 25px 25px 25px;
        }
    }
    .top2{
        float: left;
        text-align: center;
