        width:100%;
        height:auto;
        background-color:#F8F9F9;
        margin-top:65px;
        border-radius:15px 15px 15px 15px;
        .top_link{
            font-family:sans-serif;
            font-size:20px;
            text-decoration: none;
            padding:10px;
            font-weight: bold;
            color:#1B4F72;
        }
    }
    .tulisan_home{
        
        text-align: center;
        font-size: 40px;
        margin-top:70px;
        font-family:Georgia, serif; 
        margin-bottom:20px;
        font-weight: bold;
    }
    .tulisan_daftar{
        font-size: 40px;
        margin-top:80px;
        text-align: center;
        font-family:Georgia, serif; 
        margin-bottom:20px; 
    }
    .formBuilder{
        margin:15px;
        //sdisplay: flex;
       // border-bottom: 1px solid;
        
        .builder{
           
           margin-bottom : 30px;
            border-bottom: 1px solid;
            border-color: black;
            //display: table;
            //margin: auto;
            background-color: white;
           // text-align: center;
        }
    }
    .component-form-builder{
        .name-builder{
            margin: 10px;
