            font-family: sans-serif;
        }
        .style-builder{
            margin: 10px;
        }
        .delete-builder{
            margin:10px;
            background-color :#1B2631;
            color:white;
        }
        .button-add-builder{
            margin:10px;
            background-color :#1B2631;
            color:white;
        }
    }
    .first{
        display: flex;
        flex-wrap: wrap;
        padding: 0 4px;
        justify-content: center;
        margin:auto;
        border-top: 1px solid #1B4F72;
        width:80%;
        font-weight:bold;
        
        
        //border-bottom:0.1px;
        
         
        .column{
            margin-left: auto;
            margin-right: auto;
            padding: 0 4px;
            margin-top:15px; 
            .tulisan2{
                font-size: 20px;
