                border-color:white;
                border-radius:5px;
                text-decoration:none;
               
            }
             
            .radio2{
                // float:left;
                margin-top:50px;
                margin:auto;
                color:black;
                font-size: 22px;
                
                .radio{
                    margin-top:20px;
                    height: 25px;
                    width: 25px;
                    position: relative;
                    padding-left: 15px;
                    cursor: pointer;
                }
            }
           
        }   
        
       
       
         
    }
    .body {
        padding: 0;
        margin: 10px 0;
        background: #f2f2f2 url('https://formbuilder.online/assets/img/noise.png');
