        } 

    }
    
   
    .column_login{
        //flex: 50%;
        max-width: 25%;
        padding: 0 0px;
        margin-top : 70px;
        // margin-right: 10px;
        display: table;
        margin: auto;
        .tulisan_login{
            
            font-size: 20px;   
            margin-top: 30px;
            //margin-left:230px;
           
            .login_form {
                width: 300px;
                height: 40px;
                font-size:20px;
                background-color:rgb(255, 255, 255);
                }
            .tombol_login{
                 display: table;
               margin: auto;
                width:100px;
                height:40px;
                border-radius:10px 10px 10px 10px;
                // margin-left:90px;
