    .loading-spinner{
        animation: loading-spinner 1s linear infinite;   
     }
    @keyframes loading-spinner{
        0%{
            transform:rotate(0deg);
        }
        100%{
            transform: rotate(360deg);
        }
    }

   

    .menu_surat{
        padding:20px;
        align-items:center;
        .write_menu{
            font-family:sans-serif;
            font-size:30px;
            color: black;
        }
    }
    .tulisan_tabel{
        overflow-x :auto; //resdponsive
        font-size:25px;
        color:black;
        font-family:Georgia, serif;
        font-weight:bold;
        margin-top:60px;
        text-align:center;
        margin-bottom: 400px;
        bottom:20px;
        .tabel_edit{
            //margin:auto;//rata tengah
            border-collapse: collapse; //responsive
            position:block;
            align-items:center;
            justify-content: center;
            width: 70%;
            margin-left: auto;
            margin-right:auto;
            position:block;
            
            .atas_atas{
                font-weight: bold;
                background-color:#641E16;
                color:#f6f8f8;
                border-radius: 3px 3px 0px 0px;
                border-bottom: 1px solid #332a2a;
                font-size: 17px;
                padding: 25px 30px;
            }
            .atas{
                text-align:left;
                border-bottom: 2px solid #d3d7db; 
                color:#1B2631;
                padding: 25px 30px;
                font-family:sans-serif;
                background-color:#ffffff;            
                font-size:16px;
                .tabel_edit{
                    width:25px;
                    height:25px;
                    margin-right: 25px;
                    align-items: center; 
                    background-size:contain;
                    background-position:center;
                    background: cover;  
                    background-image: url('../images/pencil.png');
                
                }
                .tabel_hapus{
                    text-decoration: none;
                    align-items: center;
                    width: 25px;
                    height: 25px;
                    margin-left:24px;
                    background-size:contain;
                    background-position:top;
                    background: cover;  
                    aspect-ratio: auto 136 / 48; 
                    background-image: url('../images/trash.png');
                
                }
                

            
            }
        
        }
    }
    .higest_score{

        margin-left: auto;
        margin-right: auto;
        margin-top:50px;
        width: 60%;
        margin-bottom: 20px;
        
        font-size: 15px;
        .report{
            font-weight: bold;
            color: #fc0404;
            font-size: 20px;
    
        }
    }
    .write_report{
        margin-top: 50px;
        text-align: center;
        font-size: 40px;
        font-weight: bold;
        overflow-x :auto; //resdponsive
   
        .table_report{
            margin-top: 100px;
            margin-left:auto; 
            margin-right:auto;
            border-collapse: collapse; //responsive
            position:block;
            align-items:center;
            justify-content: center;
            width: 60%;
            .th_report{
                border-bottom:1px solid;
                border-color: rgb(124, 35, 35);
                background-color: #7E5109;
                padding: 20px 30px;
                color: white;
                font-size: 20px;
            }
            .td_report{
                border-bottom:1px solid;
                border-color: rgb(124, 35, 35);
                background-color: #d5d7d8;
                padding: 20px 30px;
                color: rgb(0, 0, 0);
                text-align: center;
                font-size: 20px;
            }

        }
    }
    
 
    .background-popup{
        width: 100%;
        height: 100%;
        background-color: rgb(0, 0, 0, 0.7);
        position: absolute;
        top: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        .popup {
            background-color: rgb(253, 250, 250);
            padding: 40px 30px;
            color:rgb(22, 19, 19);
            border-radius: 5px;
            .write-popup{
                display: flex;
                justify-content: center;
                align-items: center;
                font-size: 20px;
            }
            .close {
                display: block;
                background-image: url('../images/exit.png');
                width:30px;
                height:30px;
                margin-left: auto; 
                background-size:contain;
                background-position:top;
                background: cover; 
            }
        }
    }
    .tombol_popup{
        display: flex;
        margin-top: 50px;
        margin: auto;
        padding:10px 30px;
        border-radius:10px 10px 10px 10px;
        justify-content: center;
        align-items: center;
        background-color:#1B2631;
        color:white;
        font-size:20px;
        margin-bottom:250px; 
    }
    
        
